/* MAIN NAV*/
.mainNav{

  float: left;
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: auto;
  margin-bottom: auto;


  a{
    font-size: 24px;
    font-family: 'vanadine';
    text-transform: uppercase;
  }


  @include tablet{

    width: auto;
    overflow: auto;
    margin: 0;
    flex-direction: row;
    justify-content: space-between;

    a{

      display: inline-table !important;
      font-family: Roboto;
      font-size: 16px;
      line-height: 15px;
      text-transform: lowercase;
      padding: 15px;

    }

  }

  @include mobile{

    width: 100%;

  }

}//.main-nav